import React, {useState, useEffect, useRef} from 'react';
import axios from 'axios';
import { useAsync } from "react-async"
import { DataGrid } from '@material-ui/data-grid';

const columns = [
    { field: 'id', headerName: 'ID', width: 70 },
    { field: 'collectionId', headerName: 'Collection Id', width: 200 },
    { field: 'collection', headerName: 'collection', width: 200 },
    { field: 'rarity', headerName: 'Rarity', width: 200 },
    { field: 'rarityId', headerName: 'Rarity ID', width: 200 },
    { field: 'schema', headerName: 'Schema', width: 200 },
    { field: 'value', headerName: 'Value', width: 200 },
    { field: 'realValue', headerName: 'RealValue', width: 200 },
    { field: 'wax', headerName: 'Wax', width: 200 },
    { field: 'saleId', headerName: 'SaleId', renderCell: (params) => (
            <a href={`https://wax.atomichub.io/market/sale/${params.value}`} target="_blank">{params.value}</a>
        ), width: 250 },
    { field: 'profit', headerName: 'AE/WAX', width: 250, type: 'number' },
];


const List = (props) => {

    const { repos, pools } = props;
    if (!repos || repos.length === 0) return <p>No repos, sorry</p>;
    let i = 0;

    const arrayOfRows = [];
    if (pools) {
        repos.forEach(repo => {
            if(Array.isArray(repo.rarities) && repo.rarities.length > 0) {
                repo.rarities.forEach(rarity => {
                    i = i+1;
                    const pool = pools.find((p)=> p.id === repo.collection);
                    if(pool) {
                        arrayOfRows.push({
                            id: i,
                            collectionId: repo.id,
                            collection: repo.collection,
                            rarity: rarity.rarity,
                            schema: repo.schema,
                            rarityId: repo.rarity_id,
                            value: (+rarity.one_asset_value)/10000,
                            realValue: (+rarity.one_asset_value)*1*((+pool.fraction.split(" AETHER")[0])/pool.staked),
                            wax: 0,
                            saleId: 0,
                            profit: 0,
                        })
                    }
                });
            }
        });

    }

    const arrayRef = useRef(arrayOfRows);
    const counts = 0;
    const countRef = useRef(counts);
    const [rows, setRows] = useState(arrayRef.current);
    const [count, setCount] = useState(countRef.current);

    console.log(rows);
    return (
        <div>

            {count} / {arrayOfRows.length}
            <div style={{ height: '100vh', width: '100%' }}>
                <DataGrid rows={rows} columns={columns} pageSize={100} />
            </div>
        </div>

    );
};
export default List;
